import Vue from 'vue'
import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'
import { ACCESS_TOKEN } from "@/store/mutation-types"

// axios
const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 15000 // 
})

// request
service.interceptors.request.use(config => {
  const token = Vue.ls.get(ACCESS_TOKEN)
  if (token) {
    config.headers['X-Access-Token'] = token // token 
  }
  if(config.method=='get'){
    if(config.url.indexOf("sys/dict/getDictItems")<0){
      config.params = {
        _t: Date.parse(new Date())/1000,
        ...config.params
      }
    }
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
  /**
  * code200 
  */
    const res = response.data
    console.log(res);
    if (res.code !== 200 && !res.success) {
      // 401:;
      if (res.code === 401||res.code === 403) {
        MessageBox.confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload()// vue-router bug
          })
        })
      }
      if(res.code === 500){
        const token = Vue.ls.get(ACCESS_TOKEN)
        //notification.error({ message: '', description:'Token!',duration: 4})
        if(token && res.message=="Token"){
          // update-begin- --- author:scott ------ date:20190225 ---- for:Token----
          // store.dispatch('Logout').then(() => {
          //     window.location.reload()
          // })
          // Modal.error({
          //   title: '',
          //   content: '',
          //   okText: '',
          //   mask: false,
          //   onOk: () => {
              // store.dispatch('Logout').then(() => {
                Vue.ls.remove(ACCESS_TOKEN)
                window.location.reload()
              // })
          //   }
          // })
          // update-end- --- author:scott ------ date:20190225 ---- for:Token----
        }
        return response.data
      }
      return Promise.reject('error')
    } else {
      return response.data
    }
  },
  error => err(error)
)

const err = (error) => {
  console.log('error');
  if (error.response) {
    let data = error.response.data
    const token = Vue.ls.get(ACCESS_TOKEN)
    console.log("------------",token)
    console.log("------------",error.response.status)
    switch (error.response.status) {
      case 403:
        notification.error({ message: '', description: '',duration: 4})
        break
      case 500:
        //notification.error({ message: '', description:'Token!',duration: 4})
        if(token && data.message=="Token"){
          Vue.ls.remove(ACCESS_TOKEN)
          window.location.reload()
        }
        break
      case 404:
          notification.error({ message: '', description:'!',duration: 4})
        break
      case 504:
        notification.error({ message: '', description: ''})
        break
      case 401:
        notification.error({ message: '', description:'',duration: 4})
        if (token) {
          store.dispatch('Logout').then(() => {
            setTimeout(() => {
              window.location.reload()
            }, 1500)
          })
        }
        break
      default:
        notification.error({
          message: '',
          description: data.message,
          duration: 4
        })
        break
    }
  }
  return Promise.reject(error)
};


export default service



